.cart-container {
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.cart-body {
  background-color: white;
  height: 100vh;
  width: 350px;
  position: absolute;
  border: 1px black solid;
  z-index: 100;
  display: flex;
  flex-direction: column;
}

.cart-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.291);
  button {
    background-color: transparent;
    border: none;
    font-size: 1.5rem;
  }
  p {
    margin-left: 10px;
  }
}

.bag {
  display: flex;
  align-items: center;
}

.shipping-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
}

.border {
  border: 1px black solid;
  height: 7px;
  border-radius: 10px;
  margin-top: 10px;
}

.bar {
  background-color: black;
  height: 7px;
  width: 50%;
  border-radius: 10px;
}

// Cart Items

.item-full {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.item-text {
    width: 120px;
}

.item-container {
    p:first-of-type {
        font-weight: 100;
        padding-top: 5px;
    }
    padding: 10px;

}

.cart-image {
  img {
    width: 100%;
    height: 100px;
    border-radius: 10px;
  }
}

.add-sub {
  border: 1px solid black;
  input {
    border: none;
    width: 10px;
    &:focus {
      outline: none;
    }
  }
}

.quantity {
  margin-top: 10px;
  display: flex;
  button {
    width: 30px;
    height: 30px;
    background-color: #e1e8ee;
    border-radius: 6px;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.quantity input {
  -webkit-appearance: none;
  border: none;
  text-align: center;
  width: 32px;
  font-size: 16px;
  color: #43484d;
  font-weight: 300;
}

button:focus,
input:focus {
  outline: 0;
}

.subtotal {
    display: flex;
    justify-content: space-between;
}

.checkout {
    padding: 20px;
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    border-top: 1px solid rgba(0, 0, 0, 0.291);
    bottom: 0;
    button {
        background-color: black;
        color: white;
        margin-top: 10px;
        border: none;
        border-radius: 20px;
        height: 50px;
        font-size: 1rem;
    }
}

.test {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.components {
    overflow: scroll;
    height: 100vh;
}

